<template>
  <div class="location">
    <div class="box">
      <el-form>
        <el-form-item>
          <label>轮播图列表</label>

          <el-button class="subBtn" type="primary" @click="addCategories">添加轮播图</el-button>
          <el-button
            class="subBtn"
            style="background-color: #f56c6c;border: 0;"
            type="primary"
            @click="deleteSelection"
          >删除选中</el-button>
        </el-form-item>
      </el-form>
      <template>
        <el-table :data="entityList" @selection-change="handleSelectionChange" style="width: 100%">
          <el-table-column type="selection" width="55"></el-table-column>

          <el-table-column prop="id" label="id" width="55">
            <!-- <template slot-scope="scope">
            <img :src="scope.row.picture" height="50" />
            </template>-->
          </el-table-column>
          <el-table-column label="图片" width="100">
            <template slot-scope="scope">
              <img :src="scope.row.picture" :alt="scope.row.title" width="100" />
            </template>
          </el-table-column>
          <el-table-column label="标题">
            <template slot-scope="scope">
              <p>{{scope.row.title}}</p>
            </template>
          </el-table-column>
          <el-table-column label="内容">
            <template slot-scope="scope">
              <p>{{scope.row.content}}</p>
            </template>
          </el-table-column>
          <el-table-column label="链接">
            <template slot-scope="scope">
              <p>
                <a :href="scope.row.link" target="_blank">{{scope.row.link}}</a>
              </p>
            </template>
          </el-table-column>

          <el-table-column label="操作" width="150">
            <template slot-scope="scope">
              <el-button size="mini" @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
              <el-button size="mini" type="danger" @click="handleDelete(scope.$index, scope.row)">删除</el-button>
            </template>
          </el-table-column>
        </el-table>
      </template>
      <template>
        <el-dialog title="添加轮播图" :visible.sync="addVisible">
          <el-row>
            <el-col :span="12">
              <el-upload
                class="upload-demo"
                drag
                list-type="picture"
                :limit="1"
                :action="$baseURL+'/upload/uploadFileMasterGraph'"
                :headers="headers"
                multiple
                :file-list="fileList"
                :on-success="uploadSuccess"
                :on-error="uploadError"
              >
                <i class="el-icon-upload"></i>
                <div class="el-upload__text">
                  将
                  <span style="color: red;">主图</span>拖到此处，或
                  点击上传
                  <p style="color: #005aff;">建议图片比例统一</p>
                </div>
              </el-upload>
            </el-col>
            <el-col :span="12">
              <el-form :model="entity">
                <el-form-item label="标题">
                  <el-input v-model="entity.title"></el-input>
                </el-form-item>
              </el-form>
              <el-form :model="entity">
                <el-form-item label="链接">
                  <el-input v-model="entity.link"></el-input>
                </el-form-item>
              </el-form>
            </el-col>
          </el-row>

          <el-form :model="entity">
            <el-form-item label="内容">
              <el-input v-model="entity.content"></el-input>
            </el-form-item>
          </el-form>

          <div slot="footer" class="dialog-footer">
            <el-button @click="addVisible=false">取 消</el-button>
            <el-button type="primary" @click="add">确 定</el-button>
          </div>
        </el-dialog>
      </template>
      <template>
        <el-dialog title="编辑轮播图" :visible.sync="editVisible">
          <el-row>
            <el-col :span="12">
              <el-upload
                class="upload-demo"
                drag
                list-type="picture"
                :limit="1"
                :action="$baseURL+'/upload/uploadFileMasterGraph'"
                :headers="headers"
                multiple
                :file-list="fileList"
                :on-success="uploadSuccess"
                :on-error="uploadError"
              >
                <i class="el-icon-upload"></i>
                <div class="el-upload__text">
                  将
                  <span style="color: red;">主图</span>拖到此处，或
                  点击上传
                  <p style="color: #005aff;">建议图片比例统一</p>
                </div>
              </el-upload>
            </el-col>
            <el-col :span="12">
              <el-form :model="entity">
                <el-form-item label="标题">
                  <el-input v-model="entity.title"></el-input>
                </el-form-item>
              </el-form>
              <el-form :model="entity">
                <el-form-item label="链接">
                  <el-input v-model="entity.link"></el-input>
                </el-form-item>
              </el-form>
            </el-col>
          </el-row>

          <el-form :model="entity">
            <el-form-item label="内容">
              <el-input v-model="entity.content"></el-input>
            </el-form-item>
          </el-form>

          <div slot="footer" class="dialog-footer">
            <el-button @click="editVisible=false">取 消</el-button>
            <el-button type="primary" @click="edit">确 定</el-button>
          </div>
        </el-dialog>
      </template>
    </div>
    <template>
      <div class="page">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page.sync="currentPage1"
          :page-size="page.pageSize"
          layout="total, prev, pager, next"
          :total="page.totalSize"
        ></el-pagination>
      </div>
    </template>
  </div>
</template>

<script>
export default {
  name: "filterTable",
  data() {
    return {
      fileList: [{ name: "原图片", url: "" }],
      pageSize: 7,
      page: {},
      searchStr: "",
      addVisible: false,
      editVisible: false,
      multipleSelection: [],
      entity: {},
      headers: { token: this.$store.getters.token },
      entityList: []
    };
  },
  created() {
    this.findPage(1, this.pageSize);
  },
  methods: {
    uploadSuccess(response, file, fileList) {
      console.log(response, fileList);
      if (response.ok) {
        this.entity.picture = response.data.src;
        this.$message({
          showClose: true,
          message: "上传成功！",
          type: "success"
        });
      } else {
        this.fileList = [];
        console.log("resp失败onse", fileList);
        this.$message({
          showClose: true,
          message: "上传失败！",
          type: "error"
        });
      }

      return false;
    },
    findPage(pageNum, pageSize) {
      this.$axios({
        method: "get",
        // url: "http://localhost:8888/api/login",
        // method: "get",
        url: this.$baseURL + "/rotationChart/findPage",
        params: { pageNum: pageNum, pageSize: pageSize },

        headers: {
          "Content-Type": "application/json;charset=UTF-8",
          token: this.$store.getters.token
        }
      })
        .then(response => {
          console.log(response);
          this.page = response.data;

          this.entityList = response.data.content;
        })
        .catch(error => {
          console.log(error); //请求失败返回的数据
        });
    },
    search() {
      console.log(this.searchStr);
      this.$axios({
        method: "get",
        // url: "http://localhost:8888/api/login",
        // method: "get",
        url: this.$baseURL + "/rotationChart/findBySearchStr",
        params: { searchStr: this.searchStr },
        headers: {
          "Content-Type": "application/json;charset=UTF-8",
          token: this.$store.getters.token
        }
      })
        .then(response => {
          console.log(response);
          this.entityList = response.data;
        })
        .catch(error => {
          console.log(error); //请求失败返回的数据
          this.$message({
            showClose: true,
            message: error,
            type: "error"
          });
        });
    },

    add() {
      this.addVisible = false;
      this.fileList = [];
      console.log("1234567", this.entity);

      this.$axios({
        method: "post",
        // url: "http://localhost:8888/api/login",
        // method: "get",
        url: this.$baseURL + "/rotationChart/add",
        data: this.entity,
        headers: {
          "Content-Type": "application/json;charset=UTF-8",
          token: this.$store.getters.token
        }
      })
        .then(response => {
          console.log(response);
          if (response.data.ok) {
            this.$message({
              showClose: true,
              message: response.data.message,
              type: "success"
            });
            this.findPage(1, this.pageSize);
          } else {
            this.$message({
              showClose: true,
              message: response.data.message,
              type: "error"
            });
          }
        })
        .catch(error => {
          console.log(error); //请求失败返回的数据
          this.$message({
            showClose: true,
            message: error,
            type: "error"
          });
        });

      this.entity = {};
    },
    edit() {
      this.editVisible = false;

      this.$axios({
        method: "put",
        // url: "http://localhost:8888/api/login",
        // method: "get",
        url: this.$baseURL + "/rotationChart/updateById",
        data: this.entity,
        headers: {
          "Content-Type": "application/json;charset=UTF-8",
          token: this.$store.getters.token
        }
      })
        .then(response => {
          console.log(response);
          if (response.data.ok) {
            this.$message({
              showClose: true,
              message: response.data.message,
              type: "success"
            });

            this.findPage(this.page.pageNum, this.pageSize);
          } else {
            this.$message({
              showClose: true,
              message: response.data.message,
              type: "error"
            });
          }
        })
        .catch(error => {
          console.log(error); //请求失败返回的数据
          this.$message({
            showClose: true,
            message: error,
            type: "error"
          });
        });
      this.fileList = [];
      this.entity = {};
    },
    addCategories() {
      this.entity = {};
      this.addVisible = true;
      console.log("添加");
      this.fileList = [];
    },
    handleEdit(index, row) {
      this.editVisible = true;
      console.log(index, row);
      this.entity = row;
      this.fileList[0].url = row.picture;
    },
    handleDelete(index, row) {
      this.$axios({
        method: "delete",
        // url: "http://localhost:8888/api/login",
        // method: "get",
        url: this.$baseURL + "/rotationChart/delete",
        data: row,
        headers: {
          "Content-Type": "application/json;charset=UTF-8",
          token: this.$store.getters.token
        }
      })
        .then(response => {
          console.log(response);
          if (response.data.ok) {
            this.$message({
              showClose: true,
              message: response.data.message,
              type: "success"
            });

            this.findPage(this.page.pageNum, this.pageSize);
          } else {
            this.$message({
              showClose: true,
              message: response.data.message,
              type: "error"
            });
          }
        })
        .catch(error => {
          console.log(error); //请求失败返回的数据
          this.$message({
            showClose: true,
            message: error,
            type: "error"
          });
        });

      console.log(index, row);
    },
    deleteSelection() {
      console.log(this.multipleSelection);
      for (let index in this.multipleSelection) {
        this.$axios({
          method: "delete",
          // url: "http://localhost:8888/api/login",
          // method: "get",
          url: this.$baseURL + "/rotationChart/delete",
          data: this.multipleSelection[index],
          headers: {
            "Content-Type": "application/json;charset=UTF-8",
            token: this.$store.getters.token
          }
        })
          .then(response => {
            console.log(response);
            if (response.data.ok) {
              this.$message({
                showClose: true,
                message: response.data.message,
                type: "success"
              });
              if (this.page.pageNum > 1) {
                this.page.pageNum = this.page.pageNum - 1;
              }
              this.findPage(this.page.pageNum, this.pageSize);
            } else {
              this.$message({
                showClose: true,
                message: response.data.message,
                type: "error"
              });
            }
          })
          .catch(error => {
            console.log(error); //请求失败返回的数据
            this.$message({
              showClose: true,
              message: error,
              type: "error"
            });
          });
      }
    },
    filterHandler(value, row, column) {
      const property = column["property"];
      return row[property] === value;
    },
    formatter(row, column) {
      return row.address;
    },
    filterTag(value, row) {
      return row.tag === value;
    },
    handleSelectionChange(val) {
      console.log(val);
      this.multipleSelection = val;
    },
    handleSizeChange(val) {
      console.log(`每页 ${val} 条`);
    },
    handleCurrentChange(val) {
      console.log(`当前页: ${val}`);
      this.findPage(val, this.pageSize);
    }
  }
};
</script>

<style scoped>
.box,
.location {
  position: relative;
  height: 94%;
  overflow: scroll;
}
.location {
  /* margin-bottom: 18px; */
  height: 100%;
}
p {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.search {
  width: 70%;
}
.subBtn {
  width: 10%;
  margin: 0 auto;
  float: right;
  margin-left: 10px;
}
.page {
  width: 100%;
  position: absolute;
  bottom: 0;
  background-color: #fff;
  overflow: hidden;
  text-align: center;
}
</style>



